import { action, thunk } from 'easy-peasy'

export const register = {

  register: thunk(async (actions, payload, { dispatch }) => {
    const workYears = payload.workYears.split(' ')

    const data = {
      email: payload.email,
      fio: payload.name,
      birthDate: payload.birthdate,
      password: payload.password,
      graduationYear: parseInt(payload.graduationYear),
      type: parseInt(payload.role),
      // work: payload.workPlace,
      // position: payload.workPosition,
      // workStart: `${workYears[0]}-01-01`,
      // workEnd: `${workYears[1]}-01-01`,
      // competencies: payload.competences,
      // vkUrl: payload.socialVk,
      // instagramUrl: payload.socialInsta,
      // photo: payload.photoFile !== {} ? payload.photoFile : {},
      // about: payload.about,
      // cityId: payload.cityId,
      // city: payload.city
    }

    const status = await fetch(process.env.REACT_APP_API + 'Account/Register', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      // body: data
      body: JSON.stringify(data)
    }).then(response => response.json())
      .then(response => {
        dispatch.auth.login(response)
        return true
      })
      .catch(window.alert)

    return status
  })

}

export default register