import React, { useEffect, useState } from 'react'
import './cityStats.scss'

import { useStore } from 'easy-peasy';

export default function CityStats() {
  const [cityCounter, setCityCounter] = useState(1)
  const statistics = useStore(store => store.statistics.statistics)

  useEffect(() => {
    if (!(statistics && statistics.cityCount)) return

    let sum = 0
    statistics.cityCount.forEach(city => {
      sum += city.count
    })
    setCityCounter(sum)
  }, [statistics])
  return (
    <div className="city-stats">

      {statistics && statistics.cityCount
        ? statistics.cityCount.map((item, index) =>
          <div key={index} className="city-stats-item">
            <div className="city-stats__bar">
              <div className="city-stats__name">{item.city !== null ? item.city : 'Неизвестный город'}</div>
              <div className="city-stats__percent">{((item.count / cityCounter) * 100).toFixed(2)}%</div>
              <div style={{ width: `${item.count / cityCounter * 100}%` }} className="city-stats__bar-filled"></div>
            </div>
          </div>
        )
        : <p>loading...</p>
      }

    </div>
  )
}
